import React from "react";
import Login from "./auth/Login";
import { BrowserRouter as Router, Route, Switch } from "react-router-dom";
import Headers from "./components/Headers";
import Home from "./components/Home";
import Logout from "./auth/Logout";
import Register from "./auth/Register";
import Create from "./components/post/create";
import PostDetail from "./components/post/PostDetail";
import Edit from "./components/post/Edit";
import Delete from "./components/post/delete";
import "./App.css";

function App() {
  return (
    <Router>
      <Headers />
      <Switch>
        <Route exact path="/" component={Home} />
        <Route exact path="/login" component={Login} />
        <Route exact path="/logout" component={Logout} />
        <Route exact path="/register" component={Register} />
        <Route exact path="/posts/create" component={Create} />
        <Route exact path="/posts/:slug" component={PostDetail} />
        <Route exact path="/posts/edit/:slug" component={Edit} />
        <Route exact path="/posts/delete/:slug" component={Delete} />
      </Switch>
    </Router>
  );
}

export default App;
