import React, { useState } from "react";
import axiosInstance from "../axios";
import { useHistory } from "react-router-dom";
import { NavLink } from "react-router-dom";

const Register = () => {
  const history = useHistory();
  const initialFormData = Object.freeze({
    email: "",
    username: "",
    password: "",
  });

  const [formData, updateFormData] = useState(initialFormData);

  const handleChange = (e) => {
    updateFormData({
      ...formData,
      // Trimming any whitespace
      [e.target.name]: e.target.value.trim(),
    });
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    console.log(formData);

    axiosInstance
      .post(`user/register/`, {
        email: formData.email,
        user_name: formData.username,
        password: formData.password,
      })
      .then((res) => {
        history.push("/login");
        console.log(res);
        console.log(res.data);
      });
  };
  return (
    <div className="container col-md-6 col-md-offset-3 my-5 border border-info">
      <form className="my-3">
        <div className="form-group">
          <label>Email address</label>
          <input
            type="email"
            className="form-control border border-info"
            name="email"
            placeholder="Enter Email"
            onChange={handleChange}
          />
        </div>
        <div className="form-group">
          <label>User Name</label>
          <input
            type="text"
            className="form-control border border-info"
            name="username"
            placeholder="Enter Username"
            onChange={handleChange}
          />
        </div>
        <div className="form-group">
          <label>Password</label>
          <input
            type="password"
            className="form-control border border-info"
            name="password"
            placeholder="Password"
            onChange={handleChange}
          />
        </div>
        <button type="submit" className="btn btn-info" onClick={handleSubmit}>
          Register
        </button>
        <div className="my-2">
          <small>al ready have an account login </small>
          <NavLink exact to="/login">
            <small className="ml-2 ">here</small>
          </NavLink>
        </div>
      </form>
    </div>
  );
};

export default Register;
