import React, { useState } from "react";
import axiosInstance from "../../axios";
import { useHistory } from "react-router-dom";
import Login from "../../auth/Login";

export default function Create() {
  let u_id = "";
  const st = localStorage.getItem("access_token")
    ? localStorage.getItem("access_token")
    : null;
  if (st) {
    const jwt = require("jsonwebtoken");
    const token1 = st;
    const decode1 = jwt.decode(token1);
    u_id = decode1.user_id;
  }

  const history = useHistory();
  const initialFormData = Object.freeze({
    user: u_id,
    title: "",
    content: "",
  });

  const [postData, updateFormData] = useState(initialFormData);
  const [postimage, setPostImage] = useState(null);

  const handleChange = (e) => {
    if ([e.target.name] == "image") {
      setPostImage({
        image: e.target.files,
      });
      console.log(e.target.files);
    }
    updateFormData({
      ...postData,
      [e.target.name]: e.target.value.trim(),
    });
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    let formData = new FormData();
    formData.append("user", u_id);
    formData.append("title", postData.title);
    formData.append("content", postData.content);
    if (postimage) {
      formData.append("image", postimage.image[0]);
    }
    axiosInstance.post(`posts/create/`, formData);
    history.push({
      pathname: "/",
    });
    window.location.reload();
  };

  if (!u_id) {
    return (
      <div>
        <h4 className="container col-md-5 my-3">
          You Must Be Login First To Create A Post
        </h4>
        <Login />
      </div>
    );
  }

  return (
    <div>
      <div className="container col-md-6 col-md-offset-3 my-5 border border-info">
        <form className="my-3">
          <div className="form-group">
            <label>Title</label>
            <input
              type="text"
              className="form-control border border-info"
              name="title"
              placeholder="Enter Post Title"
              onChange={handleChange}
            />
          </div>
          <div className="form-group">
            <label>Content</label>
            <textarea
              type="text"
              className="form-control border border-info"
              name="content"
              placeholder="content"
              onChange={handleChange}
            />
          </div>
          <input
            className="my-2"
            accept="image/*"
            id="image"
            onChange={handleChange}
            name="image"
            type="file"
          />
          <button
            type="submit"
            className="btn btn-primary btn btn-info"
            onClick={handleSubmit}
          >
            Submit
          </button>
        </form>
      </div>
    </div>
  );
}
