import React, { useState } from "react";
import axiosInstance from "../axios";
import { useHistory } from "react-router-dom";
import { NavLink } from "react-router-dom";

const Login = () => {
  const history = useHistory();
  const initialFormData = Object.freeze({
    email: "",
    password: "",
  });

  const [formData, updateFormData] = useState(initialFormData);

  const handleChange = (e) => {
    updateFormData({
      ...formData,
      [e.target.name]: e.target.value.trim(),
    });
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    console.log(formData);

    axiosInstance
      .post(`token/`, {
        email: formData.email,
        password: formData.password,
      })
      .then((res) => {
        localStorage.setItem("access_token", res.data.access);
        localStorage.setItem("refresh_token", res.data.refresh);
        axiosInstance.defaults.headers["Authorization"] =
          "JWT " + localStorage.getItem("access_token");
        history.push("/");
        //console.log(res);
        //console.log(res.data);
      });
  };

  return (
    <div className="container col-md-6 col-md-offset-3 my-5 border border-info">
      <form className="my-3">
        <div className="form-group">
          <label>Email address</label>
          <input
            type="email"
            className="form-control border border-info"
            name="email"
            placeholder="Enter Email"
            onChange={handleChange}
          />
        </div>
        <div className="form-group">
          <label>Password</label>
          <input
            type="password"
            className="form-control border border-info"
            name="password"
            placeholder="Password"
            onChange={handleChange}
          />
        </div>
        <button type="submit" className="btn btn-info" onClick={handleSubmit}>
          Login
        </button>
        <div className="my-2">
          <small>don't have an accout register </small>
          <NavLink exact to="/register">
            <small className="ml-2 ">here</small>
          </NavLink>
        </div>
      </form>
    </div>
  );
};

export default Login;
