import React, { useEffect, useState } from "react";
import axiosInstance from "../../axios";
import Truncate from "react-truncate";
import { Modal, Button } from "react-bootstrap";
import PostDetail from "./PostDetail";

function List() {
  const [show, setShow] = useState(false);
  const [fslug, setFslug] = useState([]);

  function f_slug(rslug) {
    setFslug(rslug);
    console.log(fslug, "from slug");
  }

  const handleClose = () => setShow(false);
  const handleShow = () => setShow(true);

  const [appState, setAppState] = useState([]);
  useEffect(() => {
    axiosInstance.get("posts/").then((res) => {
      const allPosts = res.data;
      setAppState(allPosts);
    });
  }, [setAppState]);

  const Posts = () => {
    return (
      <div>
        {appState.map((post) => (
          <div
            className="card mx-auto my-3 border:none"
            // style={{ width: "38rem" }}
            style={{
              backgroundColor: "#2B60DE",
              width: "38rem",
              color: "white",
            }}
            key={post.id}
          >
            <a
              onClick={() => {
                f_slug(post.slug);
                handleShow();
              }}
            >
              {post.image && (
                <img
                  src={post.image}
                  className="card-img-top class='img-responsive my-2"
                  style={{
                    width: "15rem",
                    height: "15rem",
                    marginLeft: "10rem",
                  }}
                />
              )}
            </a>
            <div className="card-body">
              <small>
                Posted By <strong>{post.user}</strong>
                {" at "}
                {Intl.DateTimeFormat("en-US").format(
                  new Date(post.timestamp).getTime()
                )}
              </small>
              <h5 className="card-title">{post.title}</h5>
              <Truncate
                lines={1}
                ellipsis={
                  <span className="d-flex">
                    ... {""}
                    <a
                      style={{ color: "whilte" }}
                      href="#"
                      onClick={() => {
                        f_slug(post.slug);
                        handleShow();
                      }}
                    >
                      read
                    </a>
                  </span>
                }
              >
                {post.content}
              </Truncate>
              <div>
                <Button
                  className="my-2"
                  variant="primary"
                  onClick={() => {
                    f_slug(post.slug);
                    handleShow();
                  }}
                >
                  Detail
                </Button>
              </div>
            </div>
          </div>
        ))}
      </div>
    );
  };

  function Detail() {
    return (
      <Modal animation={false} show={show} onHide={handleClose}>
        <Modal.Header
          closeButton
          style={{ backgroundColor: " #0020C2" }}
        ></Modal.Header>
        <Modal.Body style={{ backgroundColor: " #0020C2" }}>
          <PostDetail slug={fslug} />
        </Modal.Body>
        <Modal.Footer style={{ backgroundColor: " #0020C2" }}>
          <Button variant="secondary" onClick={handleClose}>
            Close
          </Button>
        </Modal.Footer>
      </Modal>
    );
  }
  return (
    <div>
      <Posts />
      <Detail />
    </div>
  );
}
export default List;
