import React, { useEffect, useState } from "react";
import axiosInstance from "../../axios";
import { useParams, NavLink } from "react-router-dom";
import { Modal, Button } from "react-bootstrap";
import Edit from "./Edit";

function PostDetail(props) {
  const [show, setShow] = useState(false);
  const [fslug, setFslug] = useState([]);

  function f_slug(rslug) {
    setFslug(rslug);
    console.log(fslug, "from slug");
  }

  const handleClose = () => setShow(false);
  const handleShow = () => setShow(true);

  const { slug } = props;
  let u_id = "";
  const st = localStorage.getItem("access_token")
    ? localStorage.getItem("access_token")
    : null;
  if (st) {
    const jwt = require("jsonwebtoken");
    const token1 = st;
    const decode1 = jwt.decode(token1);
    u_id = decode1.user_id;
  }

  const [post, setPost] = useState([]);
  // const { slug } = useParams();

  useEffect(() => {
    axiosInstance.get("posts/" + slug + "/").then((res) => {
      setPost(res.data);
    });
  }, [setPost]);

  let edt = false;
  if (u_id === post.user) {
    edt = true;
  }
  const Detail = () => {
    return (
      <div>
        <div
          className="card mx-auto my-3 border border-info"
          style={{ width: "25rem" }}
        >
          <img
            src={post.image}
            className="card-img-top img-responsive"
            alt=""
          />
          {edt && (
            <div className="d-flex">
              <div className="my-1">
                <div>
                  <Button
                    className="my-2 ml-3"
                    variant="primary"
                    onClick={() => {
                      f_slug(post.slug);
                      handleShow();
                    }}
                  >
                    Edit
                  </Button>
                </div>
              </div>
              <div className="my-1">
                <NavLink
                  className="nav-link "
                  exact
                  to={"/posts/delete/" + post.slug}
                >
                  <button className="btn btn-danger">Delete</button>{" "}
                  <span className="sr-only">(current)</span>
                </NavLink>
              </div>
            </div>
          )}
          <div className="card-body">
            <h5 className="card-title">{post.title}</h5>
            <p className="card-text">{post.content}</p>
          </div>
        </div>
      </div>
    );
  };

  function EditPost() {
    return (
      <Modal animation={false} show={show} onHide={handleClose}>
        <Modal.Header
          closeButton
          style={{ backgroundColor: " #0020C2" }}
        ></Modal.Header>
        <Modal.Body style={{ backgroundColor: " #0020C2" }}>
          <Edit slug={slug} />
        </Modal.Body>
        <Modal.Footer style={{ backgroundColor: " #0020C2" }}>
          <Button variant="secondary" onClick={handleClose}>
            Close
          </Button>
        </Modal.Footer>
      </Modal>
    );
  }

  return (
    <div>
      <Detail />
      <EditPost />
    </div>
  );
}
export default PostDetail;
