import React, { useState } from "react";
import PostList from "./post/PostList";
import { NavLink } from "react-router-dom";
import Create from "./post/create";
import { Modal, Button } from "react-bootstrap";

function Home() {
  const [show, setShow] = useState(false);

  const handleClose = () => setShow(false);
  const handleShow = () => setShow(true);

  function CreateP() {
    return (
      <div>
        <div style={{ backgroundColor: " #0020C2" }}>
          <div className="col-sm-4 container ">
            <Button
              className=" btn btn-primary col-5 l-10 "
              onClick={handleShow}
            >
              Create Post
            </Button>
          </div>
        </div>

        {/* <Button className="my-3 " variant="primary" onClick={handleShow}>
          Create Post
        </Button> */}

        <Modal animation={false} show={show} onHide={handleClose}>
          <Modal.Header
            closeButton
            style={{ backgroundColor: " #0020C2" }}
          ></Modal.Header>
          <Modal.Body style={{ backgroundColor: " #0020C2" }}>
            <Create />
          </Modal.Body>
          <Modal.Footer style={{ backgroundColor: " #0020C2" }}>
            <Button variant="secondary" onClick={handleClose}>
              Close
            </Button>
          </Modal.Footer>
        </Modal>
      </div>
    );
  }

  return (
    <div>
      <div style={{ backgroundColor: " #0020C2" }}>
        <CreateP />
        <PostList />
      </div>
    </div>
  );
}
export default Home;

